import React, { Component } from "react";
import { Select, Button } from "antd";
import * as ApiProvider from "../../API/ApiProvider";
import moment from "moment";
// import Notelist from './Notelist'
// import searchlogo from '../../Image/svg/Search.svg'
import * as Wardstate from "../../Function/Wardstate";
import "./Note.scss";
// import X2JS from 'x2js';
// import * as PR from '../../Resource/PRGTXQRY'

var dataRange = [];
class Noteview extends Component {
  state = {
    StartTime: null,
    EndTime: null,
    display_data: null,
    context: null,
    contentdisplay: false,

    ADMDISP: [],
    ADMLIST: [],
    DISLIST: [],
    DISDISP: [],
    DTAROTQ4: [],
    DTASOAPQ: [],
    PCASELIST: [],
    ERDISP: [],
    PRGTXQRY: [],
    PRGTXQRYDIS: [],
    CPSLIST: [],
    CPSDISP: [],
    QPNOT: [],
    QPNOTDIS: [],
    OPSORAQ1: [],
    OPSORAQ2: [],
    SelectClass: 0
  };
  componentDidMount() {
    this.apiData();
  }

  SelectADMDISP(ADISTNO, ADICASE) {
    ApiProvider.getStoredProcedureADMDISP(ADISTNO, ADICASE)
      .then(data => { this.setState({ ADMDISP: data.data }); })
      .catch(err => { console.log("Andy test getStoredProcedureADMDISP NG " + JSON.stringify(err)); });
  }
  SelectDISDISP(CID) {
    const { caseinfo } = this.props;
    ApiProvider.getStoredProcedureDISDISP(caseinfo.HISID, CID)
      .then(data => {
        this.setState({
          DISDISP: data.data
        });
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureADMDISP NG " + JSON.stringify(err)
        );
      });
  }

  SelectERDISP(CID) {
    const { caseinfo } = this.props;
    ApiProvider.getStoredProcedureERDISP(caseinfo.HISID, CID)
      .then(data => {
        this.setState({
          ERDISP: data.data
        });
      })
      .catch(err => {
        console.log("Andy test getTableHBED NG " + JSON.stringify(err));
      });
  }
  SelectDTASOAPQ(date, dpt) {
    const { caseinfo } = this.props;
    //門診紀錄內容 需要時間和醫生編號
    ApiProvider.getStoredProcedureDTASOAPQ(caseinfo.HISID, date, dpt)
      .then(data => {
        this.setState({
          DTASOAPQ: data.data
        });
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureDTASOAPQ NG " + JSON.stringify(err)
        );
      });
  }
  SelectPRGTXQRYDIS(List) {
    this.setState({
      PRGTXQRYDIS: List
    });
  }
  SelectCPSDISP(HID, CID, SEQ) {
    ApiProvider.getStoredProcedureCPSDISP(HID, CID, SEQ)
      .then(data => {
        this.setState({
          CPSDISP: data.data
        });
      })
      .catch(err => {
        console.log("Andy test getTableHBED NG " + JSON.stringify(err));
      });
  }
  SelectQPNOTDIS(data) {
    this.setState({
      QPNOTDIS: data
    });
  }

  SelectOPSORAQ1(OPABGNDT, OPATIMES) {
    const { caseinfo } = this.props;
    //門診紀錄內容 需要時間和醫生編號
    ApiProvider.getStoredProcedureOPSORAQ2(caseinfo.HISID, OPABGNDT, OPATIMES)
      .then(data => {
        this.setState({
          OPSORAQ2: data.data
        });
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureDTASOAPQ NG " + JSON.stringify(err)
        );
      });
  }
  apiData() {
    const { caseinfo } = this.props;
    //入院並摘
    const ADMLIST = ApiProvider.getStoredProcedureADMLIST(caseinfo.HISID, "DOC3924B")
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureADMLIST NG " + JSON.stringify(err)
        );
      });
    //出院並摘
    const DISLIST = ApiProvider.getStoredProcedureDISLIST(
      caseinfo.HISID,
      "DOC3924B"
    )
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureDISLIST NG " + JSON.stringify(err)
        );
      });

    //門診紀錄清單
    const DTAROTQ4 = ApiProvider.getStoredProcedureDTAROTQ4(caseinfo.HISID)
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureDTAROTQ4 NG " + JSON.stringify(err)
        );
      });

    //急診紀錄清單
    const PCASELIST = ApiProvider.getStoredProcedurePCASELIST(
      caseinfo.HISID,
      "DOC3924B",
      "E"
    )
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log("Andy test getTableHBED NG " + JSON.stringify(err));
      });

    const CPSLIST = ApiProvider.getStoredProcedureCPSLIST(
      caseinfo.HISID,
      "DOC3924B"
    )
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log("Andy test getTableHBED NG " + JSON.stringify(err));
      });

    function nullvalue(value) {
      if (value === null) {
        return "911";
      }
      const PRGTXQRYDATE = moment(value.PLOCDT, "YYYYMMDD").format("YYYYMM");
      const D = PRGTXQRYDATE.substr(3, 3);
      return D;
    }

    const LIVEPLOC = nullvalue(caseinfo.PLOCSTART);
    const PRGTXQRY = ApiProvider.getStoredProcedurePRGTXQRY(
      caseinfo.HISID,
      LIVEPLOC,
      caseinfo.CASEID
    ).then(data => {
      return data.data;
    }).catch(err => {
      console.log("Andy test getTableHBED NG " + JSON.stringify(err));
    });
    const transdt = caseinfo.TRANSINDT;
    const ST = moment(transdt, "YYYYMMDD").format("YYYYMMDDHHmm");
    const ET = moment().format("YYYYMMDDHHmm");

    const QPNOT = ApiProvider.getQPNOT(caseinfo.CASEID, ST, ET)
      .then(data => {
        const parseSource = Wardstate.XM2JSON(data.data);
        return parseSource;
      })
      .catch(err => {
        console.log(err);
      });
    const OPSORAQ1 = ApiProvider.getStoredProcedureOPSORAQ1(caseinfo.HISID)
      .then(data => {
        return data.data;
      })
      .catch(err => {
        console.log(err);
      });
    Promise.all([
      ADMLIST,
      DISLIST,
      DTAROTQ4,
      PCASELIST,
      PRGTXQRY,
      CPSLIST,
      QPNOT,
      OPSORAQ1
    ]).then(result => {
      this.setState({
        ADMLIST: result[0],
        DISLIST: result[1],
        DTAROTQ4: result[2],
        PCASELIST: result[3],
        PRGTXQRY: result[4],
        CPSLIST: result[5],
        QPNOT: result[6],
        OPSORAQ1: result[7]
      });
    });
  }

  callbackFunction = item_array => {
    this.setState({
      display_data: item_array.NoteTime,
      context: item_array.Content,
      contentdisplay: true
    });
  };

  handleChange = e => {
    let value = parseInt(e, 10);
    this.setState({
      SelectClass: value
    });
  };

  onClickSearch() {
    if (dataRange.length !== 0) {
      this.setState({
        StartTime: dataRange[0],
        EndTime: dataRange[1]
      });
    }
  }

  onDateChange = (date, dateString) => {
  };

  NotecontrolBar() {
    const select_data = [
      { item: "入院病摘", value: 0 },
      { item: "出院病摘", value: 1 },
      { item: "門診紀錄", value: 2 },
      { item: "急診紀錄", value: 3 },
      { item: "病程紀錄", value: 4 },
      { item: "會診紀錄", value: 5 },
      { item: "護理紀錄", value: 6 },
      { item: "手術記錄", value: 7 }
    ];

    const items = (
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "100px 100px 100px 100px",
          columnGap: "10px",
          marginBottom: "10px",
          gridAutoColumns: "100px",
          gridAutoFlow: "column"
        }}>
        {select_data.map(info => {
          return (
            <Button
              onClick={e => this.handleChange(e.target.value)}
              className="topMenu"
              style={{
                borderRadius: "4px",
                color: "#001a6d88",
                border: "1px solid #001a6d88",
                paddingLeft: "4px",
                paddingRight: "4px",
                height: "30px",
                gridTemplateColumns: "column",
                cursor: "pointer"
              }}
              value={info.value}
            >
              {info.item}
            </Button>
          );
        })}
      </div>
    );

    return items;
  }

  renderContentArray() {
    const source = this.state.SelectClass;
    if (source === 0) {
      const data = this.state.ADMDISP;
      if (data.length === 0) {
        return null;
      } else {
        const fakeADMData = data;
        return fakeADMData.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>{row.ADMDATA}</div>
        ));
      }
    }
    if (source === 1) {
      const data = this.state.DISDISP;
      if (data.length === 0) {
        return null;
      } else {
        const fakeADMData = data;
        return fakeADMData.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>{row["1"]}</div>
        ));
      }
    }
    if (source === 2) {
      const data = this.state.DTASOAPQ;
      if (data.length === 0) {
        return null;
      } else {
        let textType = [];
        let textObj = [];
        for (var i = 0; i < data.length; i++) {
          //find type
          const type = data[i].DTSOAPTY;
          const lines = data[i].DTSOAPSQ;
          const content = data[i].DTSOAPTX;
          if (textType.indexOf(type) === -1) {
            //new type
            const newObj = {
              type: type,
              lines: lines,
              content: content
            };
            textObj.push(newObj);
            textType.push(type);
          } else {
            //old type
            const idx = textObj.findIndex(item => item.type === type);
            textObj[idx].lines = lines;
            textObj[idx].content = textObj[idx].content + "\n" + content;
          }
        }
        let textResult = [];
        for (let index = 0; index < textObj.length; index++) {
          textResult.push(textObj[index].type);
          textResult.push(textObj[index].content);
          textResult.push("\n");
        }

        return textResult.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>{row}</div>
        ));
      }
    }
    if (source === 3) {
      const data = this.state.ERDISP;
      if (data.length === 0) {
        return null;
      } else {
        const fakeADMData = data;
        return fakeADMData.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>
            {JSON.stringify(row, null, "\t")}
          </div>
        ));
      }
    }
    if (source === 4) {
      const data = this.state.PRGTXQRYDIS;
      if (data.length === 0) {
        return null;
      } else {
        var newList = [];
        for (let index = 0; index < 5; index++) {
          if (index === 0) {
            const dataFilter = data.filter(function (info, index, arr) {
              return info.PRGTYPE.toString().trim() === "D";
            });
            if (dataFilter.length !== 0) {
              newList.push(dataFilter[0]);
            }
          }

          if (index === 1) {
            const dataFilter = data.filter(function (info, index, arr) {
              return info.PRGTYPE.toString().trim() === "S";
            });
            if (dataFilter.length !== 0) {
              newList.push(dataFilter[0]);
            }
          }

          if (index === 2) {
            const dataFilter = data.filter(function (info, index, arr) {
              return info.PRGTYPE.toString().trim() === "O";
            });
            if (dataFilter.length !== 0) {
              newList.push(dataFilter[0]);
            }
          }

          if (index === 3) {
            const dataFilter = data.filter(function (info, index, arr) {
              return info.PRGTYPE.toString().trim() === "A";
            });
            if (dataFilter.length !== 0) {
              newList.push(dataFilter[0]);
            }
          }

          if (index === 4) {
            const dataFilter = data.filter(function (info, index, arr) {
              return info.PRGTYPE.toString().trim() === "P";
            });
            if (dataFilter.length !== 0) {
              newList.push(dataFilter[0]);
            }
          }
        }
        const dataMap = newList.map(function (info, index, arr) {
          return (
            <div key={index} style={{ marginBottom: "10px" }}>
              <div style={{ whiteSpace: "pre-wrap" }}>{info.PRGTYPE}</div>
              <div style={{ whiteSpace: "pre-wrap" }}>{info.PRGTX}</div>
            </div>
          );
        });
        return dataMap;
      }
    }
    if (source === 5) {
      const data = this.state.CPSDISP;
      if (data.length === 0) {
        return null;
      } else {
        const fakeADMData = data;
        return fakeADMData.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>{row["1"]}</div>
        ));
      }
    }
    if (source === 6) {
      const data = this.state.QPNOTDIS;
      if (data.length === 0) {
        return null;
      } else {
        // Time: moment(v.rec_dat.toString() + v.rec_tim.toString(), "YYYYMMDDHHmm").format("MM/DD HH:mm"),
        // special: v.special,
        // a: v.cre_nam,
        // b: v.pro_not
        const fakeADMData = data.map(function (info, index, arr) {
          const time = info.Time;
          const cre_nam = info.cre_nam;
          const pro_not = info.pro_not;
          return (
            <div style={{ marginBottom: "10px" }}>
              <div>{time}</div>
              <div>{cre_nam === undefined ? "--" : cre_nam}</div>
              <div>{pro_not === undefined ? "--" : pro_not}</div>
            </div>
          );
        });
        return fakeADMData;
      }
    }
    if (source === 7) {
      const data = this.state.OPSORAQ2;
      if (data.length === 0) {
        return null;
      } else {
        const fakeADMData = data;
        return fakeADMData.map(row => (
          <div style={{ whiteSpace: "pre-wrap" }}>{row.CONTENT}</div>
        ));
      }
    } else {
      return null;
    }
  }
  switchListRender() {
    function groupfirstarray(arrayList) {
      var result = arrayList.filter(function (value, index, arr) {
        const res = arr.map(function (value, index, arr) {
          return moment(
            value.PRGDT.toString() + value.PRGTM.toString(),
            "YYYY-MM-DDHH:mm:ss"
          ).valueOf();
        });
        return (
          res.indexOf(
            moment(
              value.PRGDT.toString() + value.PRGTM.toString(),
              "YYYY-MM-DDHH:mm:ss"
            ).valueOf()
          ) === index
        );
      });
      var arrList = [];
      for (let index = 0; index < result.length; index++) {
        const element = result[index];
        const comparevalue = moment(
          element.PRGDT.toString() + element.PRGTM.toString(),
          "YYYY-MM-DDHH:mm:ss"
        ).valueOf();
        const filterarray = arrayList.filter(function (value, index, arr) {
          return (
            moment(
              value.PRGDT.toString() + value.PRGTM.toString(),
              "YYYY-MM-DDHH:mm:ss"
            ).valueOf() === comparevalue
          );
        });
        arrList.push(filterarray);
      }
      return arrList;
    }
    function grouparray(arrayList) {
      var result = arrayList.filter(function (value, index, arr) {
        const res = arr.map(function (value, index, arr) {
          return moment(value.rec_dat.toString(), "YYYYMMDD").valueOf();
        });
        return (
          res.indexOf(
            moment(value.rec_dat.toString(), "YYYYMMDD").valueOf()
          ) === index
        );
      });
      var arrList = [];
      for (let index = 0; index < result.length; index++) {
        const element = result[index];
        const comparevalue = moment(
          element.rec_dat.toString(),
          "YYYYMMDD"
        ).valueOf();
        const filterarray = arrayList.filter(function (value, index, arr) {
          return (
            moment(value.rec_dat.toString(), "YYYYMMDD").valueOf() ===
            comparevalue
          );
        });
        arrList.push(filterarray);
      }
      return arrList;
    }
    const select = this.state.SelectClass;
    if (select === 0) {
      const data = this.state.ADMLIST;
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          const HID = object.ADISTNO.toString().trim();
          const CID = object.ADICASE.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                borderRadius: "4px",
                marginBottom: "4px",
              }}
              onClick={() => this.SelectADMDISP(HID, CID)}
            >
              <div style={{ fontSize: "14px" }}>
                住院日:{moment(object.ADIDATE.toString().trim(), "YYYYMMDD").format("YYYY/MM/DD")}
              </div>
              <div style={{ fontSize: "14px" }}>科別:{object.ADISECT}</div>
            </div>
          );
        });
        return (
          // <Notelist data={data} display_data={this.state.display_data} parentCallback={this.callbackFunction} start={this.state.StartTime} End={this.state.EndTime}></Notelist>
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 1) {
      const data = this.state.DISLIST;
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          // const HID = object.ADISTNO.toString().trim()
          const CID = object.DCASENO.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectDISDISP(CID)}
            >
              <div style={{ fontSize: "14px" }}>
                出院日:{moment(object.DDATEOUT.toString().trim(), "YYYY-MM-DD").format(
                  "YYYY/MM/DD"
                )}
              </div>
              <div style={{ fontSize: "14px" }}>
                科別:{object.DADMSECT.toString().trim()}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 2) {
      const data = this.state.DTAROTQ4;
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          const DPT = object.DTPDEPT.toString().trim();
          const DTPDATE = object.DTPDATE.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectDTASOAPQ(DTPDATE, DPT)}
            >
              <div style={{ fontSize: "14px" }}>
                門診日期:{moment(object.DTPDATE.toString().trim(), "YYYY-MM-DD").format(
                  "YYYY/MM/DD"
                )}
              </div>
              <div style={{ fontSize: "14px" }}>
                科別:{object.SECTNM.toString().trim() + object.DTPDEPT.toString().trim()}
              </div>
              <div style={{ fontSize: "14px" }}>
                醫師:{object.DTPDOCNM.toString().trim() + object.DTPDOCID.toString().trim()}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 3) {
      const data = this.state.PCASELIST;
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          const CID = object.PCASENO.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                textAlign: "center",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectERDISP(CID)}
            >
              <div style={{ fontSize: "14px" }}>
                看診日:{moment(object.PDISDT.toString().trim(), "YYYYMMDD").format(
                  "YYYY/MM/DD"
                )}
              </div>
              {/* //修改 */}
              <div style={{ fontSize: "14px" }}>
                科別:{moment(object.PDISDT.toString().trim(), "YYYYMMDD").format(
                  "YYYY/MM/DD"
                )}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 4) {
      const data = groupfirstarray(this.state.PRGTXQRY);
      if (data.length === 0) {
        return null;
      } else {
        const List = data.map(function (info, index, arr) {
          const mapList = info.map(function (v, i, a) {
            return {
              PRGTX: v.PRGTX,
              PRGTYPE: v.PRGTYPE
            };
          });
          return {
            List: mapList,
            DT: info[0].PRGDT,
            TN: info[0].PRGTM
          };
        });
        const cellRander = List.map((object, index, arr) => {
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                textAlign: "center",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectPRGTXQRYDIS(object.List)}
            >
              <div style={{ fontSize: "14px" }}>
                {moment(
                  object.DT.toString() + object.TN.toString(),
                  "YYYY-MM-DD HH:mm:ss"
                ).format("MM/DD HH:mm")}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 5) {
      const data = this.state.CPSLIST;
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          const CID = object.CPSCASE.toString().trim();
          const SEQ = object.CPSOSEQ.toString().trim();
          const HID = object.CPSHIST.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                textAlign: "center",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectCPSDISP(HID, CID, SEQ)}
            >
              <div style={{ fontSize: "14px" }}>
                {moment(object.CPSENTDT.toString().trim(), "YYYYMMDD").format(
                  "YYYY/MM/DD"
                )}
              </div>
              <div style={{ fontSize: "14px" }}>
                {moment(object.CPSENTTM.toString().trim(), "HHmm").format(
                  "HH:mm"
                )}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 6) {
      const data = this.state.QPNOT
      if (data === undefined) {
        return null
      }
      if (data.length === 0) {
        return null;
      } else {
        const List = grouparray(data);
        const mapList = List.map(function (info, index, arr) {
          const mapList = info.map(function (v, i, a) {
            return {
              Time: moment(
                v.rec_dat.toString() + v.rec_tim.toString(),
                "YYYYMMDDHHmm"
              ).format("MM/DD HH:mm"),
              cre_nam: v.cre_nam,
              pro_not: v.pro_not
            };
          });
          return {
            List: mapList,
            DT: info[0].rec_dat.toString().trim()
          };
        });
        const cellRander = mapList.map((object, index, arr) => {
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                textAlign: "center",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectQPNOTDIS(object.List)}
            >
              <div style={{ fontSize: "14px" }}>
                {moment(object.DT, "YYYYMMDD").format("YYYY/MM/DD")}
              </div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
    if (select === 7) {
      const data = []
      if (data.length === 0) {
        return null;
      } else {
        const cellRander = data.map((object, index, arr) => {
          const OPABGNDT = object.OPABGNDT.toString().trim();
          const OPACOD1 = object.OPACOD1.toString().trim();
          const OPAMANNM = object.OPAMANNM.toString().trim();
          const OPATIMES = object.OPATIMES.toString().trim();
          return (
            <div
              key={index}
              className="leftMenu"
              tabIndex="0"
              style={{
                padding: "4px 8px",
                border: "1px solid rgb(204, 204, 204)",
                cursor: "pointer",
                textAlign: "center",
                borderRadius: "4px",
                marginBottom: "4px"
              }}
              onClick={() => this.SelectOPSORAQ1(OPABGNDT, OPATIMES)}
            >
              <div style={{ fontSize: "14px" }}>{OPABGNDT}</div>
              <div style={{ fontSize: "14px" }}>{OPACOD1}</div>
              <div style={{ fontSize: "14px" }}>{OPAMANNM}</div>
            </div>
          );
        });
        return (
          <div>{cellRander}</div>
        );
      }
    }
  }
  render() {
    const control = this.NotecontrolBar();
    const renderContentArray = this.renderContentArray();
    const listRender = this.switchListRender();
    return (
      <div className="Notelayout">
        {control}
        <div>
          <div
            style={{
              display: "grid",
              gridTemplateColumns: "240px 1fr",
              gridGap: "10px"
            }}
          >
            <div>
              <div
                style={{
                  backgroundColor: "#71b0e46e",
                  height: "36px",
                  paddingLeft: "4px",
                  borderTopRightRadius: "4px",
                  borderTopLeftRadius: "4px",
                  color: "rgba(0,0,0,0.7)",
                  alignItems: "center",
                  width: "240px"
                }}
              >
                <div
                  style={{
                    fontSize: "16px",
                    color: "rgba(0,0,0,0.8)",
                    lineHeight: "36px"
                  }}
                >
                  資料清單
                </div>
              </div>
              <div
                style={{
                  padding: "10px",
                  maxHeight: "700px",
                  overflow: "auto"
                }}
              >
                {listRender}
              </div>
            </div>
            <div style={{ overflow: "auto" }}>
              <div
                style={{
                  backgroundColor: "#71b0e46e",
                  height: "36px",
                  paddingLeft: "4px",
                  borderTopRightRadius: "4px",
                  borderTopLeftRadius: "4px",
                  fontSize: "16px",
                  color: "rgba(0,0,0,0.7)",
                  alignItems: "center"
                }}
              >
                <div
                  style={{
                    fontSize: "16px",
                    color: "rgba(0,0,0,0.8)",
                    lineHeight: "36px"
                  }}
                >
                  文件內容
                </div>
              </div>
              <div style={{ maxHeight: "700px", overflow: "auto" }}>
                {renderContentArray}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Noteview;
