import React, { Component } from 'react';
import './Nonlabviewtable.scss'
import NonlabviewtableCell from './NonlabviewtableCell'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'
import { Modal } from 'antd';
import InsReportModal from '../ExamReportModal'
import * as ORDERLIST from '../../Resource/ORDERLIST'

class Nonlabviewtable extends Component {
    state = {
        visible: false,

        modalVisible: false,
        modalReport: "",
        ORDLIST: [],
        RESSECT: []
    }
    componentDidMount() {
        this.nonLabApiGet()
    }
    nonLabApiGet() {
        function selectMonthInterval(storeProcedureTime) {
            const timestampInterval = moment() - moment(storeProcedureTime, "YYYY-MM-DD").valueOf()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        // ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
        //     .then(data => { this.setState({ RESSECT: data.data }) })
        //     .catch(err => { console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err)) });
        const { caseinfo, StartTime } = this.props
        const storeProcedureTime = moment(StartTime).format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(storeProcedureTime)
        ApiProvider.getStoredProcedureORDLIST(caseinfo.HISID, storeProcedureTime, "ALL", "DOC3924B")
            .then(data => { this.setState({ ORDLIST: data.data }) })
            .catch(err => { console.log("getStoredProcedureORDLIST NG " + JSON.stringify(err)) });
        // this.setState({ ORDLIST: ORDERLIST.data })
    }
    showModal = (report) => {
        this.setState({
            modalVisible: true,
            modalReport: report
        });
    };

    handleOk = e => {
        this.setState({
            modalVisible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            modalVisible: false,
        });
    };
    render() {
        function ORDLISTFormat(ORDLIST, StartTime, EndTime) {
            var ORDLIST_Format = []
            if (Array.isArray(ORDLIST)) {
                for (let index = 0; index < ORDLIST.length; index++) {
                    const element = ORDLIST[index];
                    const elemetnStatus = Number(element.ORSTATUS)
                    const elementORBGNTime = moment(element.ORBGNDT.toString().trim() + element.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    if (StartTime <= elementORBGNTime && elementORBGNTime <= EndTime) {
                        if (elemetnStatus === 64) {
                            //正式報告
                            ORDLIST_Format.push({
                                time: elementORBGNTime,
                                status: 0,
                                element: element
                            })
                        }
                        else if (elemetnStatus === 38) {
                            //已簽收 
                            ORDLIST_Format.push({
                                time: elementORBGNTime,
                                status: 1,
                                element: element
                            })
                        }
                        else if (elemetnStatus === 31) {
                            //待送檢
                            ORDLIST_Format.push({
                                time: elementORBGNTime,
                                status: 2,
                                element: element
                            })
                        }
                        else if (elemetnStatus === 82 || elemetnStatus === 60 || elemetnStatus === 61) {
                            //已取消 不顯示
                            continue
                        }
                        else {
                            //Other
                            ORDLIST_Format.push({
                                time: elementORBGNTime,
                                status: elemetnStatus,
                                element: element
                            })
                        }
                    }
                }
            }
            return ORDLIST_Format
        }
        const { modalVisible, modalReport, ORDLIST } = this.state
        const { StartTime, EndTime } = this.props
        let FormatList = ORDLISTFormat(ORDLIST, StartTime, EndTime)
        const showModal = this.showModal
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel

        FormatList = FormatList.sort((a, b) => {
            return a.time < b.time ? 1 : -1
        })

        console.log(FormatList)
        const nonlabcell = FormatList.map(function (value, index, array) {
            const come = value.element.ORSPCN1;
            const time = value.time
            const name = value.element.ORPROCED;
            const status = value.status;
            return (
                <NonlabviewtableCell key={"Nonlabviewtable" + index} come={come} time={time} name={name} statis={status} hisId={value.element.ORHISTNO} caseId={value.element.ORDSEQCN} month={value.element.ORMM} seq={value.element.ORDSEQNO} showModal={showModal}></NonlabviewtableCell>
            );
        })
        const title = ["時間", "檢驗項目", "檢體", "狀態", "報告"]
        const titlecell = title.map(function (value, index, arr) {
            const tempStyle = index == 1
            return (
                <t3 className='nonlabtebletitlecell' style={{ justifyContent: "center" }}>{value}</t3>
            );
        })


        return (
            <div>
                <div style={{ height: "30px", border: "rgba(238,238,238,1) 1px solid", backgroundColor: "rgba(226, 237, 243,.5)", display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                    {titlecell}
                </div>
                {JSON.stringify(FormatList) !== '[]' ?
                    <div className='nonlabviewtable'>{nonlabcell} </div>
                    :
                    <div style={{ height: "50px", paddingLeft: "3.5%", display: "flex", alignItems: "center" }}>
                        無資料
                    </div>}
                <Modal
                    title={"檢驗結果"}
                    visible={modalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1000}
                    footer={null}>
                    <InsReportModal report={modalReport}></InsReportModal>
                </Modal>
            </div>
        );
    }
}

export default Nonlabviewtable
