import React, { Component } from 'react';
import Treatmentordertablecell from './Treatmentordertablecell'
import './Treatmentordertable.scss'

class Treatmentordertable extends Component {
    render() {
        const { TRTMNT01, caseinfo, PickerDate, searchbar } = this.props

        if (TRTMNT01 === null || TRTMNT01 === undefined) {
            return null
        }
        // const DataList = TRTMNT01 //SortDataList(TRTMNT01, PickerDate)
        const Treatmentordertablecelllist = TRTMNT01.map(
            (info, index) => <Treatmentordertablecell caseinfo={caseinfo} key={index} info={info}></Treatmentordertablecell>

        )
        const titlearray = ['時間起迄', '處置名稱', '備註內容', '頻次', '狀態']
        const titlecell = titlearray.map(function (info, index) {
            return (
                <t3 className="treatmentordertablecellstyle" key={index}>{info}</t3>
            );
        })

        return (
            <div>
                <div className='treatmentordertable'>
                    {titlecell}
                </div>
                <div style={{ minHeight: "120px", maxHeight: searchbar ? "calc(100vh - 300px)" : "400px", overflowY: "auto" }}>
                    {JSON.stringify(TRTMNT01) !== '[]' ?
                        Treatmentordertablecelllist
                        :
                        <div style={{ height: "50px", paddingLeft: "4%", display: "flex", alignItems: "center" }}>
                            無資料
                    </div>}
                </div>
            </div>
        );
    }
}

export default Treatmentordertable
